<template>
  <div class="bottom">
    <div class="left" @click="mybtn">
      <img src="~assets/img/cart/tick.svg" alt="" :class="{isActive:noselect}">
    </div>
    <div class="center">
      <span class="select">全选</span>
      <span class="compute">合计:￥{{money}}</span>
    </div>
    <div class="right">
      <span class="num">去计算({{complate}})</span>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    name: "CartBottom",
    computed: {
      ...mapGetters(['cartlist']),
      noselect() {
        return !(this.cartlist.filter(item => !item.check).length)
      },
      money() {
        return this.cartlist.filter(item => {
          return item.check
        }).reduce((preValue, item) => {
          return preValue + item.count * ((item.price).toString().slice(1));
        }, 0).toFixed(2)
      },
      complate() {
        let count = 0;
        this.cartlist.filter(item => {
          if(item.check) {
            count += item.count
          }
        })
        return count
      }
    },
    methods: {
      mybtn() {
        //1.判断是否有未选中的按钮
        let isSelectAll = this.cartlist.find(item => !item.check);

        // 2.有未选中的内容, 则全部选中
        if (isSelectAll) {
          this.cartlist.forEach(item => {
            item.check = true;
          });
        } else {
          this.cartlist.forEach(item => {
            item.check = false;
          })
        }
      }
    }
  }
</script>

<style scoped>
  .bottom{
    width: 100%;
    height: 44px;
    position: fixed;
    bottom: 50px;
    left: 0;
    right: 0;
    display: flex;
  }
  .bottom>div{
    height: 100%;
    line-height: 44px;
    text-align: center;
    background-color: #EEE;
  }
  .left{
    flex: 1;
  }
  .left img {
    width: 18px;
    border-radius: 50%;
    border: 1px solid pink;
    margin-top: 12px;
  }
  .center{
    flex: 4.5;
    position: relative;
    background-color: peru;
  }
  .bottom .right{
    flex: 2;
    background-color: #FF4500;
  }
  .select{
    position: absolute;
    left: -5px;
    top: 0px;
    font-size: 14px;
    color: #A9A9A9;
  }
  .compute{
    position: absolute;
    left: 40px;
    color: #666;
  }
  .num{
    color: #fff;
    font-weight: 600;
    font-size: 14px;
  }
  .isActive{
    background-color: #FF8198;
  }
</style>